.loading{
    width: 100%;
}
.loading h1{
    margin-top: 200px;
    font-family: 'Montserrat', sans-serif;
    font-size: 4rem;
    text-align: center;
    letter-spacing: 7px;
    color: rgb(187, 147, 18);
    text-transform: uppercase;
}
span{
    margin: 0 5px;
    font-size: 5rem;
    display: inline-block;
   
}
span:nth-child(1) {
    -webkit-animation: animate1 1s infinite;
            animation: animate1 1s infinite;
    -webkit-animation-delay: .3s;
            animation-delay: .3s;
}
span:nth-child(2) {
    -webkit-animation: animate2 1s infinite;
            animation: animate2 1s infinite;
}
span:nth-child(3) {
    -webkit-animation: animate3 1s infinite;
            animation: animate3 1s infinite;
    -webkit-animation-delay: .4s;
            animation-delay: .4s;
}
@-webkit-keyframes animate1{
   0%{
       -webkit-transform: translateY(0px);
               transform: translateY(0px);
   }
   50%{
       -webkit-transform: translateY(-20px);
               transform: translateY(-20px);
   }
   100%{
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
}
}
@keyframes animate1{
   0%{
       -webkit-transform: translateY(0px);
               transform: translateY(0px);
   }
   50%{
       -webkit-transform: translateY(-20px);
               transform: translateY(-20px);
   }
   100%{
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
}
}
@-webkit-keyframes animate2{
    0%{
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
    }
    50%{
        -webkit-transform: translateY(-20px);
                transform: translateY(-20px);
    }
    100%{
     -webkit-transform: translateY(0px);
             transform: translateY(0px);
 }
}
@keyframes animate2{
    0%{
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
    }
    50%{
        -webkit-transform: translateY(-20px);
                transform: translateY(-20px);
    }
    100%{
     -webkit-transform: translateY(0px);
             transform: translateY(0px);
 }
}
@-webkit-keyframes animate3{
    0%{
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
    }
    50%{
        -webkit-transform: translateY(-20px);
                transform: translateY(-20px);
    }
    100%{
     -webkit-transform: translateY(0px);
             transform: translateY(0px);
 }
}
@keyframes animate3{
    0%{
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
    }
    50%{
        -webkit-transform: translateY(-20px);
                transform: translateY(-20px);
    }
    100%{
     -webkit-transform: translateY(0px);
             transform: translateY(0px);
 }
}
